@import url(https://fonts.googleapis.com/css?family=VT323);


#cursor {
  border-left: .1em solid white;
  animation: blink .7s steps(1) infinite;
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}
.row {
    display: flex;
    margin-bottom: 2em;
}

.main {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.main > * {
    margin-top: 1em;
    margin-bottom: 1em;
}
  
.column {
    flex: 50% 1;
}

.all > * {
    margin-bottom: 2em;
}

.wrapper {
    width: 100%;
    overflow: auto;
    padding: 1%;
  }
  
  
  /* Container to make absolute positioning easier on psuedo element */
  .image_container {
    position: relative;
    overflow: auto;
    padding: 0;
    margin: 0;
  }
  
  .image_container img {
    width: 30em;
    height: 20em;
    display: block;
  }
  
  /* Structure for ::before element */
  .image_container::before {
    background: rgba(0, 0, 0, 0.5);
    position: absolute;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    display: block;
    opacity: 0;
    color: white;
    padding: 10px;
    transition: 0.5s ease;
    content: 'Click To Enlarge';
    text-align: center;
    font-size: large;
  }
  
  /* Hover state for container to show ::before on mouseover */
  .image_container:hover::before {
    opacity: 1;
    cursor: pointer;
  }
  
  /* Classes for full page version */

  .cci_cover {
    background: 
    linear-gradient(
    rgba(0, 0, 0, 0.7),
    rgba(0, 0, 0, 0.7)
    ),
    url(/static/media/cci.6be01224.jpg);
    background-size: cover;
    height: 100vh;
}


  .cci_box {
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  .cci_row {
    display: flex;
    justify-content: center;
  }
  
  .cci_text {
    font-size: 1.5em;
    margin: 1em;
    color: rgb(255, 255, 255);
    background: rgba(1, 25, 29, 0.5);
    padding: 4px 8px;
    max-width: 50em;
    text-align: center;
}

.cci_cover img {
  width: 100%;
  height: auto;
}
